<template>
    <div class="mBg">
        <div class="moHead">
            <mhead-top titleHead="查看物流" lfIcon="el-icon-arrow-left icon" lfUrl="back" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        </div>
        <div class="mo-distrib">
            <div class="mo-di-line">
                <i class="el-icon-truck icon"></i>
                <div class="mo-di-info">
                    <div class="text">快递公司：{{info.logistics.data[0].brand}}</div>
                    <div class="date">快递单号：{{info.logistics.data[0].no}}</div>
                </div>
            </div>
        </div>
        <div class="mo-rece" v-show="info.username">
            <div class="mo-di-line">
                <i class="el-icon-location-information icon"></i>
                <div class="mo-di-info w">
                    <div class="flex f-nowrap j-bet">
                        <span>{{info.username}}</span>
                        <span>{{info.mobile}}</span>
                    </div>
                    <div class="color9">{{info.address}}</div>
                </div>
            </div>
        </div>
        <div class="md-logistic">
            <el-timeline>
                <el-timeline-item
                        v-for="(item,index) in info.logistics.data[0].data"
                        :key="index"
                        type='primary'
                        :color = "index==0?'':'#ddd'"
                        :timestamp="item.time">
                    <span :style="index==0?'color:#0bb8b2;':''">{{item.context}}</span>

                </el-timeline-item>
            </el-timeline>
        </div>
        <div class="mo-shopList">
            <h1>商品清单</h1>
            <div class="mop-item" v-for="item in info.goods">
                <div class="mop-pic">
                    <el-image class="mop-image" :src="item.pic"></el-image>
                </div>
                <div class="mop-info">
                    <div class="mop-name">{{item.name}}</div>
                    <div class="mop-spec">
                        <span>{{item.spec_name}}{{item.spec_unit}}</span>
                    </div>
                    <div class="mop-price">
                        <div class="mop-price">￥{{item.price}}</div>
                        <div class="mop-number">
                            ×{{item.number}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import mheadTop from '../../../components/mobile/headTop';
    import {getOrderInfo} from '../../../service/api';
    export default {
        name: 'mOrderDetail',
        data(){
            return{
                order_id:'',
                info:'',
                activities: [{
                    content: '活动按期开始',
                    timestamp: '2018-04-15'
                }, {
                    content: '通过审核',
                    timestamp: '2018-04-13'
                }, {
                    content: '创建成功',
                    timestamp: '2018-04-11'
                }]
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();

        },
        computed:{


        },
        methods:{
            async initData(){
                this.order_id = this.$route.query.order_id || '';
                var res = await getOrderInfo(this.order_id);
                this.info = res;
                console.log(res);
            },

        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    /*头部*/
    .moHead{
        width:100%;
        height: px2rem(90);
        position: fixed;
        top:0;
        left:0;
        background-color:#fafafa;
        border-bottom:1px solid #eee;
        z-index: 1000;
    }
    /*.mo-nav{*/
    /*    display: flex;*/
    /*    flex-wrap: nowrap;*/
    /*    justify-content: space-between;*/
    /*}*/
    /*.mo-nav .icon{*/
    /*    font-size:px2rem(52);*/
    /*    line-height:px2rem(90);*/
    /*    padding:0 px2rem(20);*/
    /*}*/
    /*.mo-edit{*/
    /*    padding-right:px2rem(20);*/
    /*    font-size: px2rem(30);*/
    /*    color:#666;*/
    /*}*/
    /*.navText{*/
    /*    height: px2rem(90);*/
    /*    line-height: px2rem(90);*/
    /*    font-size: px2rem(36);*/
    /*}*/


    /*物流*/
    .mo-distrib{
        background-color: #fff;
        padding:px2rem(20);
        margin-bottom: px2rem(20);
    }
    .mo-di-line{
        display: flex;
        flex-wrap: nowrap;
    }
    .mo-di-line .icon{
        font-size: px2rem(40);
        color: #999;
        padding-top: px2rem(8);
        width:px2rem(60);
    }
    .mo-di-info .text{
        color:$default;
    }
    .mo-di-info .date{
        color:#999;
        font-size: px2rem(26);
    }
    .arrowRight{
        position: absolute;
        right:px2rem(20);
        top:px2rem(30);
        font-size:px2rem(40);
        color:#999;
    }
    /*收货地址*/
    .mo-rece{
        background-color: #fff;
        padding:px2rem(20);
        margin-bottom: px2rem(20);
    }
    /*物流详情*/
    .md-logistic{
        background-color: #fff;
        padding:px2rem(20);
        margin-bottom: px2rem(20);
    }
    /*商品*/
    .mo-shopList{
        background-color: #fff;
        padding:px2rem(20);
        margin-bottom: px2rem(20);
    }
    .mop-item{
        background: #f5f5f5;
        padding:px2rem(10);
        margin-top:px2rem(20);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
    }
    .mop-pic{
        width:px2rem(150);
        height: px2rem(150);
        padding-right:px2rem(20);
    }
    .mop-image{
        width:px2rem(150);
        height: px2rem(150);
        object-fit: contain;
        background-color: #f2f2f2;
    }
    .mop-info{
        width:px2rem(510);
    }
    .mop-name{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        color:#333;
        line-height: px2rem(36);
        min-width: px2rem(460);
    }
    .mop-spec{
        background-color: #fafafa;
        font-size:px2rem(22);
        color:#999;
        display: inline-block;
        border-right: 3px;
        flex-wrap: nowrap;
        padding:0 px2rem(10);
        height: px2rem(36);
        line-height: px2rem(36);
        margin-top:5px;
    }
    .mop-spec span{
        display: inline-block;
        float:left;
    }
    .mop-spec .icon{
        font-size:px2rem(22);
        color:#999;
        height: px2rem(36);
        line-height: px2rem(36);
        padding-left:px2rem(6);
        float:left;
    }
    .mop-price{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }



</style>



